.Appli {
    margin: 20px 10px;
    padding: 0;
    background-color: #cdcdcd;

    a, a:visited, a:active, a:link
    {
        color: inherit;
        text-decoration: none;

        &.navActive
        {
            font-weight: bold;
            color: chartreuse;
        }
    }

    .contenuPrincipal
    {
        & > * 
        {
            margin: 40px;    
            h2
            {
                margin-bottom: 20px;
                padding-bottom: 10px;
                border-bottom: 2px solid #555;
            }
        }
    }
}